import React from 'react';

import { useFormik } from 'formik/dist/Formik';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';

export default function TextFieldControl({
  fieldName,
  label,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly label: string;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  return (
    <Box key={fieldName} sx={{ height: '75px' }}>
      <TextField
        fullWidth
        size="small"
        disabled={disabled}
        id={fieldName}
        name={fieldName}
        label={label}
        value={
          typeof formik.values[fieldName] === 'number'
            ? isNaN(formik.values[fieldName])
              ? ''
              : formik.values[fieldName]
            : formik.values[fieldName] || ''
        }
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        error={formik.touched[fieldName] && Boolean(formik.errors[fieldName])}
        helperText={
          formik.touched[fieldName] && `${formik.errors[fieldName] || ''}`
        }
      />
    </Box>
  );
}
